html {
  font-size: 14px;
}

@media screen and (min-width: 1380px) {
  .html {
    font-size: 16px;
  }
}

.App {
  display: grid;
  grid-template-columns: 1fr  704px 1fr;
  grid-template-areas:
    ". App-Header ."
    ". CardsContainer .";
  grid-row-gap: 40px;
}


